<template>
  <div class="modal" :class="{ 'is-active': active }" v-if="transaction">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">
          Patient:
          {{
            transaction.patient.first_name + " " + transaction.patient.last_name
          }}
        </p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div class="card">
          <header class="card-header">
            <p class="card-header-title">Customer Details:</p>
          </header>
          <div class="card-content">
            <div class="content">
              <p>
                <strong>Name:</strong>
                {{
                  transaction.patient.first_name +
                    " " +
                    transaction.patient.last_name
                }}
              </p>
              <p>
                <strong>Telephone:</strong>
                {{ transaction.patient.telephone }}
              </p>
              <p>
                <strong>Email:</strong>
                {{ transaction.patient.email }}
              </p>
            </div>
          </div>
        </div>
        <div class="card" style="margin-top: 1em;">
          <header class="card-header">
            <p class="card-header-title">Transaction Details:</p>
          </header>
          <div class="card-content">
            <div class="content">
              <p>
                <strong>Cost:</strong>
                {{ transaction.total_cost }}
              </p>
              <p>
                <strong>Amount Paid:</strong>
                {{ transaction.amount_paid }}
              </p>
              <p>
                <strong>Balance:</strong>
                {{ transaction.balance }}
              </p>
              <p>
                <strong>Payment method:</strong>
                {{ transaction.payment_method }}
              </p>
              <p>
                <strong>Payment Date: </strong>
                {{ formatTime(transaction.created_at) }}
              </p>
            </div>
          </div>
        </div>
        <button
          class="button is-danger is-outlined is-fullwidth"
          @click="reverse(transaction)"
          :disabled="isReversed(transaction)"
          style="margin-top: 1em;"
        >
          Reverse Transaction
        </button>
      </section>
    </div>
  </div>
</template>
<script>
export default {
  name: "TransactionComponent",
  methods: {
    formatTime(time) {
      var date = new Date(time);
      return date.toLocaleString();
    },
    closeModal() {
      this.$store.dispatch("toggleTransactionModal", false);
      this.$store.dispatch("updateCurrentTransaction", null);
    },
    reverse(transaction) {
      transaction.status = "reversed";
      this.$socket.emit("update-transaction", transaction);
      this.closeModal();
    },
    isReversed(transaction) {
      return transaction.status === "reversed";
    }
  },
  computed: {
    active() {
      return this.$store.state.cashier.isTransactionModalOn;
    },
    transaction() {
      return this.$store.state.cashier.currentTransaction;
    }
  }
};
</script>
