<template>
  <div>
    <table class="table is-fullwidth">
      <thead>
        <tr>
          <th>Time</th>
          <th>Customer</th>
          <th>Payment Method</th>
          <th>Status</th>
          <th>Total</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(transaction, key) in transactions" :key="key">
          <td>{{formatTime(transaction.created_at)}}</td>
          <td
            class="is-capitalized"
          >{{ transaction.patient.first_name + " " + transaction.patient.last_name }}</td>
          <td>{{ transaction.payment_method }}</td>
          <td>{{ transaction.status }}</td>
          <td>{{ transaction.total_cost }}</td>
          <td>
            <button class="button is-link" @click="viewTransaction(transaction)">View Transaction</button>
          </td>
        </tr>
      </tbody>
    </table>
    <transaction-component></transaction-component>
  </div>
</template>

<script>
import TransactionComponent from "@/components/TransactionComponent";
export default {
  name: "PaymentRequestListingComponent",
  created() {
    this.$store.dispatch(
      "fetchTransactions",
      this.$store.state.authentication.inMemoryToken
    );
    let store = this.$store;
    this.$socket.on("create-transaction", transaction => {
      store.dispatch("addTransaction", transaction);
    });
  },
  methods: {
    formatTime(time) {
      var date = new Date(time);
      return date.toLocaleString();
    },
    viewTransaction(transaction) {
      this.$store.dispatch("updateCurrentTransaction", transaction);
      this.$store.dispatch("toggleTransactionModal", true);
    }
  },
  components: {
    "transaction-component": TransactionComponent
  },
  computed: {
    transactions() {
      return this.$store.state.cashier.transactions;
    }
  }
};
</script>